@import url(https://fonts.googleapis.com/css2?family=Ubuntu:ital,wght@0,300;0,400;0,700;1,400&display=swap);
.App {
  text-align: center;
}

.row {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}

.fondonoche {
  background: url("https://feriasycongresos.s3-sa-east-1.amazonaws.com/ag/fondo-vectorial-noche-ag.png") no-repeat center top;
  background-size: 140vw;
  background-color: rgb(9, 31, 50);
}


.fondodia {
  background: url("https://feriasycongresos.s3-sa-east-1.amazonaws.com/ag/fondo-vectorial-dia-ag.png") no-repeat center top;
  background-size: 100vw;
  background-color: white;
  
}


@media screen and (max-width:960px) {
  .fondonoche, .fondodia {
    background-size: 160vw;
    background-position-y:-5%;
  }   
}

:root {
  --main-color: #2f6fa0;
  --cold-color: #47aefe;
  --hot-color: #fc6554;
  --text-color: rgb(52, 52, 52);
  --font-family: 'Ubuntu', sans-serif, 'Helvetica Neue',
  sans-serif;
  --gradiente-color: linear-gradient(to left, #2f6fa0, #47aefe);
}

a:-webkit-any-link {
  text-decoration: none;
  color: inherit;
}
.inputsearch {
    border: none;
    border-radius: 0 8px 0  0;
    border: 1px solid var(--main-color);
    outline: none;
    font-family: var(--font-family);
    line-height: 18px;
}

.searchbutton {
    border: 1px solid white;
    border-radius: 0 8px 0 0;
    background-color: var(--main-color);
    margin-left: 5px;
    color: white;
    line-height: 18px;
}

.searchbutton:hover {
    border: 1px solid var(--main-color);
    background-color: white;
    color: var(--main-color);
}
#logoHenry {
  margin-right: 20px;
}

.navbar{
  text-decoration: none;
  color: white;
  background: var(--gradiente-color);
  padding-top: 15px;
}

.navlinks {
  text-decoration: none;
  color: white;
  padding-bottom: 10px;
}

.navlinks:hover, .navlinks:link, .navlinks:focus{
  text-decoration: none;
  border-bottom: 1px solid white;
  
}

.navElement {
  margin-bottom: 10px;
  margin-right: 10px;
}

.short-text { display: none; }

/* // When resolution <= 1200px, hide full text and show short text */
@media (max-width: 470px) {
    .short-text { display: inline-block; }
    .full-text { display: none; }
}
.cards {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: center;
          justify-content: center;
}
.card {
  position: relative;
  border: 1px solid rgb(216, 216, 216);
  width: 18.75rem;
  padding: 40px 0 10px 0;
  border-radius: 10px;
  margin: 20px;
  background-color: #fff;
  box-shadow: 2px 2px 10px rgba(70, 70, 70, 0.495);
}

.card-title {
  border-bottom: 1px solid rgb(213, 213, 213);
  margin: 0 30px 10px 30px;
  padding-bottom: 10px;
  font-size: 1.3rem;
}

#closeIcon {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  margin-right: 8px;
  margin-top: 8px;
}

.xbutton {
  position: absolute;
  top: 3px;
  right: 3px;
  background-color: var(--main-color);
  color: #fff;
  border: none;
  border-radius: 0 8px 0 8px;
  padding: 4px 10px;
}

.xbutton:hover {
  border: 1px solid var(--main-color);
  background-color: white;
  color: var(--main-color);
  }

.card-body {
  margin-top: 0px !important;
  padding-top: 0px !important;
}

.min {
  color: var(--cold-color);
}

.max {
  color: var(--hot-color);
}

.shadow {
  -webkit-filter: drop-shadow( 3px 3px 2px rgba(0, 0, 0, .5));
  filter: drop-shadow( 3px 3px 2px rgba(0, 0, 0, .5));
  /* Similar syntax to box-shadow */
}

.footer {
    background-color: "#F8F8F8";
    border-top: "1px solid #E7E7E7";
    text-align: "center";
    padding: "20px";
    position: "fixed";
    left: "0";
    bottom: "0";
    height: "60px";
    width: "100%";
    background-image: "../img/fondo-footer-base.png" no-repeat center;
}

.phantom {
  display: 'block';
  padding: '20px';
  height: '60px';
  width: '100%';
}



.footertext {
    text-decoration: none;
    color: white;
    margin-right: 30px;
    margin-bottom: 30px;
    font-size: 14px;
}

.footerlinks {
    text-decoration: none;
    color: white;
    font-size: 14px;
  }
  
  .footerlinks:hover, .footerlinks:focus{
    text-decoration: none;
    border-bottom: 1px solid white;
    
  }

  @media screen and (max-width:325px) {
    .footertext {
        margin-right: 5px;
    }
}
  
